.app-header {
   display: flex;
   justify-content: space-between;
   align-items: center;
   align-content: center;
   padding-right: 20px;
   padding-left: 20px;
   border-bottom: 1px solid #2d2d2d;
   box-shadow: 0 0 2px 0px;
   height: 70px;
   position: fixed;
   top: 0;
   right: 0;
   left: 0;
   z-index: 100;

   @include for-mobileOnly-layout {
      // 640px max
      background-color: pink;
   }

   @include for-mobile-layout {
      // 640px min
      background-color: orange;
   }

   @include for-tablet-layout {
      // 700px min
      background-color: green;
   }

   @include for-normal-layout {
      // 900px min
      background-color: gold;
   }

   @include for-wide-layout {
      // 1300px min
      background-color: tomato;
   }

   .toggleModal {
      display: none;

      @include for-mobileOnly-layout {
         // 640px max
         display: block;
         font-size: rem(55px);
         cursor: pointer;
      }

   }

   .nav-bar {
      display: none;
      @include for-mobile-layout {
         // 640px min
         display: block;
      }
   }

   nav {

      @include for-mobileOnly-layout {
         // 640px max
         display: flex;
         flex-direction: column;
         align-items: center;
         background: lightgrey;
         position: fixed;
         width: 100%;
         top: 70px;
         left: 0;
         z-index: 1030;
         transition: top 10s linear;
      }

      a {
         margin: 10px;
         padding: 10px;
         font-size: rem(19px);

         

         &:hover {
            background-color: $clr1;
            border-radius: 5px;
            color: $clr2;
         }

      }

      nav a:hover {
         color: $clr6;
      }
   }

   .right-header {
      display: flex;
      gap: 20px;
      cursor: pointer;
      font-size: rem(22px);
      padding-top: 7px;
      justify-content: flex-end;
      width: 118px;

   }
   

   .cart {
      position: fixed;
      top: 75px;
      right: 101px;
      background-color: white;
      width: 200px;
      height: 200px;
      border-radius: 10px;
      cursor: unset;
      box-shadow: 0 0 2px 2px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      padding: 10px;
      font-size: rem(13px);

      hr {
         width: 100%;
      }
   }

}